import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import MiniPalette from './MiniPalette';
import Avatar from '@material-ui/core/Avatar';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import { withStyles } from '@material-ui/styles';
import blue from '@material-ui/core/colors/blue';
import red from '@material-ui/core/colors/red';
import styles from './styles/PaletteListStyles';

class PaletteList extends Component {
	constructor(props) {
		super(props);
		this.state = {
			openDeleteDialog: false,
			deletingId: ''
		};
		this.openDialog = this.openDialog.bind(this);
		this.closeDialog = this.closeDialog.bind(this);
		this.handleDelete = this.handleDelete.bind(this);
		this.goToPalette = this.goToPalette.bind(this);
		this.restoreDefaultPalettes = this.restoreDefaultPalettes.bind(this);
	}
	openDialog(id) {
		this.setState({ openDeleteDialog: true, deletingId: id });
	}
	closeDialog() {
		this.setState({ openDeleteDialog: false, deletingId: '' });
	}
	goToPalette(id) {
		this.props.history.push(`/palette/${id}`);
	}
	handleDelete() {
		this.props.deletePalette(this.state.deletingId);
		this.closeDialog();
	}
	restoreDefaultPalettes() {
		window.localStorage.clear();
		window.location.reload();
	}
	render() {
		const { palettes, classes } = this.props;
		const { openDeleteDialog } = this.state;
		return (
			<div className={this.props.classes.root}>
				<div className={classes.container}>
					<nav className={classes.nav}>
						<h1>React Colors</h1>
						<Link to="/palette/new">Create Palette</Link>
					</nav>
					<TransitionGroup className={classes.palettes}>
						{palettes.length > 0 ? (
							palettes.map((palette) => (
								<CSSTransition key={palette.id} classNames="fade" timeout={500}>
									<MiniPalette
										{...palette}
										goToPalette={this.goToPalette}
										// deletePalette={deletePalette}
										openDialog={this.openDialog}
										key={palette.id}
										id={palette.id}
									/>
								</CSSTransition>
							))
						) : (
							<Button
								variant="contained"
								className={classes.button}
								color="default"
								onClick={this.restoreDefaultPalettes}
							>
								Restore Default Palettes
							</Button>
						)}
					</TransitionGroup>
				</div>
				<Dialog open={openDeleteDialog} aria-labelledby="delete-dialog-title" onClose={this.closeDialog}>
					<DialogTitle id="delete-dialog-title">Delete This Palette?</DialogTitle>
					<List>
						<ListItem button onClick={this.handleDelete}>
							<ListItemAvatar>
								<Avatar style={{ backgroundColor: blue[100], color: blue[600] }}>
									<CheckIcon />
								</Avatar>
							</ListItemAvatar>
							<ListItemText primary="Yes, delete!" />
						</ListItem>
						<ListItem button onClick={this.closeDialog}>
							<ListItemAvatar>
								<Avatar style={{ backgroundColor: red[100], color: red[600] }}>
									<CloseIcon />
								</Avatar>
							</ListItemAvatar>
							<ListItemText primary="No, cancel" />
						</ListItem>
					</List>
				</Dialog>
			</div>
		);
	}
}

export default withStyles(styles)(PaletteList);
